import React from "react";
import PostListItem from "../templates/post-item";

const IndexPage = ({ data }) => {
  const { edges: posts } = data.allMarkdownRemark;

  return (
    <div className="post-list">
      {posts &&
        posts.length &&
        posts
          .sort((a, b) => {
            const first =
              a.node && a.node.frontmatter && a.node.frontmatter.sortDate;
            const second =
              b.node && b.node.frontmatter && b.node.frontmatter.sortDate;
            return first && second ? Number(second) - Number(first) : 0;
          })
          .map(({ node: post }) => (
            <PostListItem
              key={post.frontmatter.path}
              title={post.frontmatter.title}
              date={post.frontmatter.humanDate}
              path={post.frontmatter.path}
              excerpt={post.frontmatter.excerpt}
              tags={post.frontmatter.tags}
            />
          ))}
    </div>
  );
};

export const query = graphql`
  query IndexQuery {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            sortDate: date(formatString: "YYMMDD")
            humanDate: date(formatString: "dd, MM/YY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`;

export default IndexPage;



